
.CircularProgressbar .CircularProgressbar-path {
  stroke: #0ef3c5 !important;
  stroke-opacity: 0.8;

}

.CircularProgressbar .CircularProgressbar-trail {
  stroke: #038298 !important;
  stroke-opacity: 0.2;
}

.CircularProgressbar .CircularProgressbar-text {
  fill: #0ef3c5 !important;
  font-size: 20px;
  align-content:center;
  opacity: 0.9;
}

.CircularProgressbar {
  top:-1;
  width: 50vw;
  height: 50vh;
  min-height: 300px;
  min-width: 300px;
}

.parent {
  position: relative;
  height: 60%;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items:center;
}
.child {
  position:absolute;
  margin: auto auto;
  display:inline-block;
}
.timer {
  min-width: 50px;
  min-height: 50px;
  width: 100vw;
  height: 100vh;
  position:relative;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.timer_button {
  text-align: center;
}


.text, .activeInput, .disabledInput {
  color: #0ef3c5;
  font-size:min(150px,max(10vw, 20px));
  opacity: 1;
}

.activeInput{
  width:2ch;
  background: transparent;
  position: relative;
  border: 1px solid #FFF;
  border: none;
  display: inline-block;
}

.disabledInput{
  width:2ch;
  background: transparent;
  position: relative;
  border: 1px solid #FFF;
  border:none;
  display: inline-block;
}