.top {
  background-image:  url("./images/Meteor.svg");
  height:100vh;
  width:100vw;
  margin:0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #172347;
}

html, body, .full_size {
  height: 100vh;
  width: 100vw;
}


button {
  background-color: transparent;
  color: rgba(255,255,255,0.8);
  border:none;
  width: 5vw;
  min-width: 75px;
  opacity: 0.5;
  transition:0.2s;
}

button:hover{
  opacity: 1;
  transition:0.8;
}
button:active{
  opacity: 1;
  transition:0.8s;
}
