body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.top {
  background-image:  url(/pomo-react/static/media/Meteor.1da8e478.svg);
  height:100vh;
  width:100vw;
  margin:0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #172347;
}

html, body, .full_size {
  height: 100vh;
  width: 100vw;
}


button {
  background-color: transparent;
  color: rgba(255,255,255,0.8);
  border:none;
  width: 5vw;
  min-width: 75px;
  opacity: 0.5;
  transition:0.2s;
}

button:hover{
  opacity: 1;
  transition:0.8;
}
button:active{
  opacity: 1;
  transition:0.8s;
}


.CircularProgressbar .CircularProgressbar-path {
  stroke: #0ef3c5 !important;
  stroke-opacity: 0.8;

}

.CircularProgressbar .CircularProgressbar-trail {
  stroke: #038298 !important;
  stroke-opacity: 0.2;
}

.CircularProgressbar .CircularProgressbar-text {
  fill: #0ef3c5 !important;
  font-size: 20px;
  align-content:center;
  opacity: 0.9;
}

.CircularProgressbar {
  top:-1;
  width: 50vw;
  height: 50vh;
  min-height: 300px;
  min-width: 300px;
}

.parent {
  position: relative;
  height: 60%;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items:center;
}
.child {
  position:absolute;
  margin: auto auto;
  display:inline-block;
}
.timer {
  min-width: 50px;
  min-height: 50px;
  width: 100vw;
  height: 100vh;
  position:relative;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.timer_button {
  text-align: center;
}


.text, .activeInput, .disabledInput {
  color: #0ef3c5;
  font-size:min(150px,max(10vw, 20px));
  opacity: 1;
}

.activeInput{
  width:2ch;
  background: transparent;
  position: relative;
  border: 1px solid #FFF;
  border: none;
  display: inline-block;
}

.disabledInput{
  width:2ch;
  background: transparent;
  position: relative;
  border: 1px solid #FFF;
  border:none;
  display: inline-block;
}
